function addToLogData(obj, state) {
  state.logData.unshift(`[${obj.timeStamp}] ${obj.function}: ${obj.message}`);
  if (state.logData.length > 100) {
      state.logData = state.logData.slice(0, 100);
  }
}

function addToSensorData(obj, state) {
  for (const key in obj) {
      const oldEntry = state.sensorData.find((x) => x.name == getDisplayName(key));
      if (oldEntry) {
          oldEntry.value = getDisplayValue(key, obj[key]);
      } else {
          state.sensorData.push({ name: getDisplayName(key), value: getDisplayValue(key, obj[key]) });
      }
  }
}

function addToConfigData(obj, state) {
  state.configData = obj;
  state.testarea.brightness = obj.matrixBrightness;
}

function addToSysInfoData(obj, state) {
  for (const key in obj) {
      const oldEntry = state.sysInfoData.find((x) => x.name === getDisplayName(key));
      if (oldEntry) {
          oldEntry.value = getDisplayValue(key, obj[key]);
      } else {
          state.sysInfoData.push({ name: getDisplayName(key), value: getDisplayValue(key, obj[key]) });
      }
      if (key === "pixelitVersion") {
          state.version = obj[key];
      }
  }
}

function getDisplayName(key) {
  switch (key) {
      case "lux":
          key = "Luminance";
          break;
      case "temperature":
          key = "Temperature";
          break;
      case "humidity":
          key = "Humidity";
          break;
      case "pressure":
          key = "Pressure";
          break;
      case "pixelitVersion":
          key = "PixelIt version";
          break;
      case "hostname":
          key = "Hostname";
          break;
      case "note":
          key = "Note";
          break;
      case "sketchSize":
          key = "Sketch size";
          break;
      case "freeSketchSpace":
          key = "Free sketch space";
          break;
      case "wifiRSSI":
          key = "Wifi RSSI";
          break;
      case "wifiQuality":
          key = "Wifi quality";
          break;
      case "wifiSSID":
          key = "Wifi SSID";
          break;
      case "ipAddress":
          key = "IP-Address";
          break;
      case "freeHeap":
          key = "Free heap";
          break;
      case "chipID":
          key = "ChipID";
          break;
      case "cpuFreqMHz":
          key = "CPU freq.";
          break;
      case "sleepMode":
          key = "Sleep mode";
          break;
  }
  return key;
}

function getDisplayValue(key, value) {
  switch (key) {
      case "lux":
          if (typeof value == "number") {
              value = value.toFixed(3) + " lux";
          }
          break;
      case "note":
          if (!value.trim()) {
              value = "---";
          }
          break;
      case "sketchSize":
      case "freeSketchSpace":
      case "freeHeap":
          value = humanFileSize(value, true);
          break;
      case "wifiRSSI":
          value += " dBm";
          break;
      case "wifiQuality":
          value += " %";
          break;
      case "cpuFreqMHz":
          value += " MHz";
          break;
      case "sleepMode":
          value = value ? "On" : "Off";
          break;
      case "temperature":
          if (typeof value == "number") {
              value = value.toFixed(1) + " C";
          }
          break;
      case "humidity":
          if (typeof value == "number") {
              value = value.toFixed(1) + " %";
          }
          break;
      case "pressure":
          if (typeof value == "number") {
              value = Math.round(value) + " hPa";
          }
          break;
  }
  return value;
}

function humanFileSize(bytes, si = false, dp = 1) {
  const thresh = si ? 1000 : 1024;

  if (Math.abs(bytes) < thresh) {
      return bytes + " B";
  }

  const units = si ? ["kB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"] : ["KiB", "MiB", "GiB", "TiB", "PiB", "EiB", "ZiB", "YiB"];
  let u = -1;
  const r = 10 ** dp;

  do {
      bytes /= thresh;
      ++u;
  } while (Math.round(Math.abs(bytes) * r) / r >= thresh && u < units.length - 1);

  return bytes.toFixed(dp) + " " + units[u];
}


